<template>
	<canvas id="canvas" width="900" height="540"></canvas>
</template>

<script>
	import image from '../assets/background.png'
	export default {
		name: "AnimatedBackground",
		mounted(){
			var canvas = document.getElementById('canvas'),
			context = canvas.getContext('2d'),
			backgroundSpeed = 35,
			backgroundOffset = 0,
			fps = 60,
			lastTime = 0,
			background = new Image();

			background.src = image;
			canvas.width = window.innerWidth;
			canvas.height = window.innerHeight;

			function drawBackground(offset){
				context.save();
				context.translate(-offset, 0);
				context.drawImage(background, 0, 1080 - background.height, 1920, 1080);

				context.drawImage(background, 0 + 1920, 1080 - background.height, 1920, 1080);
				context.restore();
			}

			function updateOffsets(){
				backgroundOffset = backgroundOffset < 1920 ? backgroundOffset + backgroundSpeed/fps : 0;
			}
			function animate() {
				var now = new Date().getTime();
				fps = 1000 / (now - lastTime);
				updateOffsets();
				drawBackground(backgroundOffset);
				requestAnimationFrame(animate);
				lastTime = now;
			}

			window.addEventListener('resize', () => {
				canvas.width = window.innerWidth;
				canvas.height = window.innerHeight;
			})
			background.onload = function() {
				requestAnimationFrame(animate);
				console.log("cargado")
			}
		}
	}
</script>
<style scoped>
canvas{
	position: fixed;
	z-index: -100;
}
</style>


