			<h1>Getting to know the editor</h1>
			<p>
				The editor has 2 screens, the first screen has 3 tabs, which are used to
				add information about the song, synchronize, and adding subtitles to the song.<br>
				The second screen allows you to manipulate the position of the lyrics on the screen.
			</p>
			<h2>Properties tab</h2>
			<p>
				There are currently 3 options in this tab. you can input the name of the song,
				choose the language support and the preview time.<br><br>
				If you're making a song in Japanese, Chinese or Korean you <strong>must</strong> select the appropiate language support. For any other purpose selecting "English" will work just fine.<br><br>
				The preview time, is the position in which the song will start in the select song menu.
			</p>
			<h2>Input tab</h2>
			<p>
				Here is where you put the lyrics that <strong>you are going to type</strong>, so they must contain any of the 26 letters of the US-Keyboard. It can also contain spaces and apostrophes in between letters, since these will be completed automatically in-game.<br>You can also put special characters such [ and ], this is explained on a latter section.<br><br>
				If you press the "Insert lyrics" button, you will get a pop-up window where you can paste lyrics. Then they will be automatically separated and put on the timing boxes.<br><br>
				To time the lyrics, you need to press the "play" button to start the song, then you can add the times of the beginning and finish of every line by using the buttons "Set start", "Set end" or manually. You can also use the keys F9 and F10 instead of the buttons.<br><br>
				Using the minus and plus sign buttons you can add a line below or delete it. Use the "Save" button to save the lyrics. They will be saved to a JSON file inside the song folder.
			</p>
			<h2>Subtitle tab</h2>
			<p>
				Very similar to the previous tab, here you press the "Insert subtitles" button and you will be able to paste the lyrics of the song. These lyrics could be anything you want and they will be updated by every line. If you want a subtitle to be the same for many lines you just paste it many times as needed.
			</p>
