<template>
	<body>
		<div class="middleBar">
			<Header class="header" title="Letras"></Header>
			<TabSelector @currentTab="changeTab($event)"/>
			<TabOne v-if="tab == 0"/>
			<TabTwo v-else-if="tab == 1"/>
			<TabThree v-else-if="tab == 2"/>
		</div>
	</body>
</template>

<script>
	import Header from './Header'
	import TabSelector from './TabSelector'
	import TabOne from './TabOne'
	import TabTwo from './TabTwo'
	import TabThree from './TabThree'
	export default {
		name: "MiddleBar",
		data() {
			return{
				tab: 0
			}
		},
		props: {
		},
		methods:{
			changeTab(value){
				this.tab = value
				console.log(value)
			}
		},
		components:{
			Header,
			TabSelector,
			TabOne,
			TabTwo,
			TabThree
		},
		mounted(){
			function GetURLParameter(sParam){
				var sPageURL = window.location.search.substring(1);
				var sURLVariable = sPageURL.split(sParam);
				return sURLVariable[1]
			}
			var current_tab = GetURLParameter("tab=")
			if(current_tab > 0 && current_tab < 3){
				this.tab = current_tab
			} else {
				this.tab = 0
			}
			
			//http://200.83.199.82:8080/?tab=0
		}
	}
</script>

<style scoped>	/*la palabra scope significa que el style es solo para este componente*/
	html, body{
		font-family: 'Roboto', sans-serif;
		color: white;
	}
	h1{
		margin-top: 0px;
		font-size: 28px;
		padding-left: 6%;
	}

	p{
		padding-left: 6%;
		padding-right: 6%;
		line-height: 1.6;
		text-align: justify;
	}

	h2{
		margin-top: 0px;
		padding-left: 6%;
		font-size: 20px;
	}

	ul{
		padding-left: 6%;
		line-height: 1.6;
	}

	img{
		width: 80%;
	}
	#playablekeys-img{
		max-width: 600px;
	}
	#scripts{
		max-width: 400px;
	}
	.middleBar{
		position: relative;
		display:  block;
		background-color: rgba(20,20,20,0.9);
		min-width: 300px;
		width: 70vw;
		height: 100%;
	}
	.header{
		clip-path: inset(0px 0px -20px 0px);
		box-shadow: 0px 0px 10px white;
	}
	a:link{
		color: white;
	}
	a:visited{
		color: white;
	}
	a:hover{
		color: yellow;
	}

@media screen and (max-width:1000px){
	body{
		overflow-x:hidden; /*para no scrollear y encontrar la barra lateral*/
	}
	.middleBar{
		width: 100vw;
	}
}
</style>
