<template>
	<div class="box">
		<ul>
			<li class="tab" @click="setTab(0)">Home</li>
			<li class="tab" @click="setTab(1)">Tutorial</li>
			<li class="tab" @click="setTab(2)">About</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name: "TabSelector",
		data() {
			return{
				tab: 0
			}
		},
		methods:{
			setTab(value){
				this.$emit('currentTab', value)
				this.tab = value
			}
		},
		props: {
		},
		components:{
		}
	}
</script>

<style scoped>	/*la palabra scope significa que el style es solo para este componente*/
.box{
	display:inline-flex;
	justify-content: flex-start;
	border-style: solid;
	border-width: 0px;
	border-color: white;
	box-shadow:
	inset 10px 0 60px #000033;
}
ul{
	display: flex;
	justify-content: space-around;
	list-style-type: none;
	padding: 0px 0px 0px 0px;
	margin: 0px;
}
li{
	padding: 10px 20px 10px 20px;
	border-style: solid;
	border-color: gray;
	border-width: 0px 2px 0px 0px;
}
h1{
	font-size: 52px;
	}
.tab:hover{
	background-color: #000080;
	cursor: pointer;
}

@media screen and (max-width:1000px){
	.box{
		display:flex;
		justify-content: center;
	}
	li{
		border-width: 0px 2px 0px 2px;
	}
	li + li{
		border-left: 0px;
	}
}
</style>
